import { Link } from "@remix-run/react";
export function ButtonLink({ link, title, nameClass, icon }) {
  return (
    <Link
      className={`anchor ${nameClass ? nameClass : ""}`}
      to={link}
      preventScrollReset={true}
    >
      {title}
    </Link>
  );
}

export function Button({ msg, isDisabled, nameClass, onClick }) {
  return (
    <button
      className={`btn ${nameClass ? `btn__${nameClass}` : ""}`}
      disabled={isDisabled}
      type="submit"
      onClick={onClick}
    >
      {msg}
    </button>
  );
}
