import { useLoaderData } from "@remix-run/react";
import { sendRequestToBackend } from "../src/utilities/utility";
import Navbar from "../src/components/Navbar";
import Hero from "../src/page/homePage/Hero";
import { Features } from "../src/page/homePage/features";
import { Details } from "../src/page/homePage/Details";
import Products from "../src/page/homePage/products";

export async function loader() {
  const response = await sendRequestToBackend("get", null, null, "products");
  return response.data;
}

export default function Home() {
  const data = useLoaderData();
  const headPhones = data?.data?.headphones;
  const laptops = data?.data?.laptops;
  return (
    <>
      <Navbar />
      <Hero />
      <Features />
      <Products title={"#Headphones"} data={headPhones} />
      <Details />
      <Products title={"#Laptops"} data={laptops} />
    </>
  );
}
