import ProductCard from "../../components/ProductCard";
import { ButtonLink } from "../../components/Button";
export default function Products({ title, data }) {
  console.log(data);
  return (
    <section className="featured-products">
      <div className="featured-products__container">
        <h2 className="secondary-heading featured-products__heading">
          {title}
        </h2>
        <div className="featured-products__products">
          {data.map((el) => (
            <ProductCard
              key={el._id}
              title={el.title.slice(0, 40).padEnd(43, "...")}
              img={el.image.replace("UL320", "UL550")}
              nameClass="featured-products__card-img"
              price={`${el.price.symbol}${el.price.value}`}
              path={`products/${el.asin}/${el._id}`}
              rating={el.rating}
            />
          ))}
        </div>
        <div className="featured-products__btn-box">
          <ButtonLink
            link="/otherproducts"
            title="see more"
            nameClass="featured-products__button"
          />
        </div>
      </div>
    </section>
  );
}
